.post-editor {
    position: relative; }

.post-editor-content [class*="post-editor__section_"]:not(:first-child),
.post-editor-column [class*="post-editor__block_"]:not(:first-child) {
    margin-top: 40px; }

.post-editor__block_image.center {
    .lazy-image-outer {
        margin-left: auto;
        margin-right: auto; } }

.post-editor__block_image.right {
    .lazy-image-outer {
        margin-left: auto; } }

.post-editor__section_8 {
    .post-editor__block_text {
        @include media(d) {
            width: 640px;
            margin-right: auto;
            margin-left: auto; } }

    .slider-caption {
        width: 100%; }

    .post-editor__block_direct-speech {
        @include media(d) {
            margin-left: 110px; } } }

.post-editor__section_6 {
    @include media(d) {
        width: 640px;
        margin-right: auto;
        margin-left: auto; }

    .slider-caption {
        width: 100%; } }

.post-editor__section_4-4 {
    @include media(d) {
        display: flex;
        justify-content: space-between; }

    .post-editor-column {
        @include media(m) {
            &:nth-child(1) {
                margin-bottom: 20px; } }

        @include media(d) {
            width: calc(50% - 10px);
            flex-shrink: 0; } }

    .slider-caption {
        width: 100%; } }

.post-editor__section_3-3 {
    @include media(d) {
        display: flex;
        justify-content: space-between;
        width: 640px;
        margin-right: auto;
        margin-left: auto; }

    .post-editor-column {
        @include media(m) {
            &:nth-child(1) {
                margin-bottom: 20px; } }

        @include media(d) {
            width: calc(50% - 10px);
            flex-shrink: 0; } }

    .slider-caption {
        width: 100%; } }

.post-editor__block_image {
    position: relative;

    @include media(m) {
        margin-left: -16px;
        margin-right: -16px; }

    .post-editor__block_caption {
        @include media(m) {
            margin-left: 16px;
            margin-right: 16px; } }

    .post-editor__img-wrap {
        display: block;
        position: relative;

        &.hoverable {
            &::after {
                content: ' ' attr(href) ' ';
                position: absolute;
                bottom: 20px;
                display: block;
                max-width: 250px;
                height: 22px;
                margin-left: 20px;
                opacity: 0;
                font-size: $mini-fz;
                color: rgba($light-c, 0.9);
                text-align: left;
                text-overflow: ellipsis;
                overflow: hidden;
                transition: opacity $transition-time; }

            &:hover {
                &::after {
                    opacity: 1; }

                .img-shadow {
                    background-color: rgba($dark-c, 0.3); } } } }

    img {
        display: block; }

    &.center {
        img {
            margin-left: auto;
            margin-right: auto; }

        .post-editor__block_caption {
            text-align: center; } }

    &.left {
        img {
            margin-right: auto; }

        .post-editor__block_caption {
            text-align: left; } }

    &.right {
        img {
            margin-left: auto; }

        .post-editor__block_caption {
            text-align: right; } } }

.post-editor__block_slider {
    .slider_format_2x1 {
        padding-bottom: 50%; }

    .slider_format_3x2 {
        padding-bottom: 66.666%; }

    .slider {
        @include media(m) {
            width: 100vw;
            max-width: 600px;
            margin-left: -16px; } }

    .slide {
        background-color: $light-gray; }

    .slider-container img {
        display: block;
        height: 100%; }

    .nav-wrapper {
        position: absolute;
        bottom: -40px;
        font-size: 0;

        @include media(m) {
            right: 8px; }

        @include media(d) {
            right: 0; } }

    .slide-nav-counter {
        color: #242434; }

    .slider-arrow {
        width: 24px;
        height: 24px;
        font-size: 12px;
        line-height: 24px;
        color: #242434;

        &:hover {
            border: 1px solid $border-c;
            border-radius: 50%; } }

    .slider-caption {
        @include media(m) {
            width: 65%; }

        @include media(d) {
            width: 75%; } } }

.post-editor-slider-block {
    position: relative;

    &__slider {
        @include media(m) {
            margin-left: -16px;
            margin-right: -16px; } }

    &__slide {
        cursor: pointer; }

    &__footer {
        display: flex;
        align-items: baseline;
        margin-top: 20px; }

    &__caption {
        margin-right: 24px;
        line-height: $mini-lh;
        letter-spacing: 0.08em;
        color: $base-dark-c;
        max-width: calc(100% - 140px);

        @include media(m) {
            font-size: 14px; }

        @include media(d) {
            font-size: 15px; } }

    &__navigation-wrap {
        display: flex;
        align-items: center;
        margin-left: auto;
        flex-shrink: 0; }

    &__navigation {
        flex-shrink: 0;
        margin: 3px 0 0; }

    &__arrow {
        width: 24px;
        height: 24px;
        font-size: 0;
        line-height: 24px;
        color: $dark-c;
        top: auto;
        bottom: -44px;
        right: 0;
        border: 1px solid transparent;
        border-radius: 50%;

        &:hover {
            border-color: #B1C1D2; }

        &::before {
            font-size: 12px; } } }

.post-editor__block_quote {
    .quote {
        border-left: 4px solid $base-c;

        @include media(m) {
            padding-top: 5px;
            padding-bottom: 5px;
            padding-left: 16px; }

        @include media(d) {
            padding-top: 10px;
            padding-bottom: 10px;
            padding-left: 25px; }

        &__title {
            margin-bottom: 5px;
            line-height: 1.2;
            font-family: $bold-f;
            color: $base-c;

            @include media(m) {
                font-size: 36px; }

            @include media(d) {
                font-size: 42px; } }

        &__text {
            margin-bottom: 0;
            line-height: $mini-lh;
            font-family: $add-light-it-f;

            @include media(m) {
                font-size: 22px; }

            @include media(d) {
                font-size: 26px; } } } }

.post-editor__block_video {
    &-wrap {
        position: relative;
        padding-bottom: calc(100% * 9 / 16); }

    iframe {
        position: absolute;
        left: 0;
        height: 100%;
        width: 100%; } }

.post-editor__block_caption {
    margin-top: 20px !important;
    line-height: $mini-lh;
    letter-spacing: 0.08em;
    color: $base-dark-c;

    @include media(m) {
        font-size: 14px; }

    @include media(d) {
        font-size: 15px; } }

.post-editor__block_separator {
    .line {
        display: flex;
        align-items: center;
        color: #7A8BA1;

        &::before,
        &::after {
            content: "";
            height: 2px;
            flex-grow: 1;
            background-color: $border-c; }

        &::before {
            margin-right: 3px; }

        &::after {
            margin-left: 3px; } } }

.direct-speech-author {
    &__avatar {
        display: block;
        border-radius: 50%;
        border: 1px solid #C3CEDF;

        @include media(m) {
            width: 64px;
            height: 64px; }

        @include media(d) {
            width: 110px;
            height: 110px; } }

    &__name {
        margin-bottom: 5px;
        font-family: $bold-f;
        font-size: $medium-fz;
        letter-spacing: 0.02em;
        color: $base-c; }

    &__description {
        font-size: $base-fz;
        font-family: $add-sb-it-f; } }

.direct-speech__text_quotes {
    position: relative;

    & > * {
        position: relative;
        z-index: 2; }

    &::before {
        content: "";
        display: block;
        width: 80px;
        height: 50px;
        position: absolute;
        top: -20px;
        z-index: 1;
        background-image: url(../images/special-projects/summer-money/svg/quotes.svg);
        background-size: contain;
        background-repeat: no-repeat; } }

.direct-speech_buble {
    @include media(m) {
        margin-left: -16px;
        margin-right: -16px;
        padding: 24px 16px;
        background-color: #f5f6f7;
        border-radius: 4px; }

    @include media(d) {
        display: flex;
        align-items: flex-start; }

    .direct-speech-author {
        &__avatar {
            @include media(m) {
                margin-right: 16px;
                margin-bottom: 16px; }

            @include media(d) {
                margin-right: 15px;
                flex-shrink: 0;

                & + .direct-speech__text-wrap {
                    margin-left: 35px;

                    &::before {
                        content: "";
                        display: block;
                        width: 35px;
                        height: 40px;
                        position: absolute;
                        left: -35px;
                        top: 40px;
                        background-image: url(../images/svg/direct-speech-triangle.svg);
                        background-repeat: no-repeat;
                        background-size: contain; } } } }

        &__description {
            margin-bottom: 10px;

            @include media(m) {
                margin-bottom: 24px;
                font-size: 16px; } } }

    .direct-speech__text-wrap {
        position: relative;

        @include media(d) {
            padding: 15px 25px;
            background-color: #f5f6f7;
            border-radius: 4px; } } }

.direct-speech_base {
    .direct-speech-author {
        display: flex;
        align-items: center;

        &__avatar {
            margin-right: 20px; } }

    .direct-speech__text {
        margin-top: 40px;
        font-size: $mini-fz; } }

.direct-speech_base_with-avatar {
    .direct-speech__text {
        margin-left: 20px; }

    .direct-speech__text_quotes {
        &::before {
            left: -20px; } } }

.direct-speech_base_without-avatar {
    .direct-speech__text_quotes {
        &::before {
            left: -40px; } } }

.img-shadow {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    transition: background-color $transition-time; }

.indent-wrap {
    display: inherit;
    justify-content: inherit;
    width: 100%;
    flex-grow: 1;

    &_mt1 {
        margin-top: 1em; }

    &_mt2 {
        margin-top: 2em; }

    &_mt3 {
        margin-top: 3em; }

    &_mt4 {
        margin-top: 4em; }

    &_mt5 {
        margin-top: 5em; }


    &_mb1 {
        margin-bottom: 1em; }

    &_mb2 {
        margin-bottom: 2em; }

    &_mb3 {
        margin-bottom: 3em; }

    &_mb4 {
        margin-bottom: 4em; }

    &_mb5 {
        margin-bottom: 5em; }

    &_pt2 {
        padding-top: 2em; }


    &_pt1 {
        padding-top: 1em; }

    &_pt3 {
        padding-top: 3em; }

    &_pt4 {
        padding-top: 4em; }

    &_pt5 {
        padding-top: 5em; }

    &_pb1 {
        padding-bottom: 1em; }


    &_pb2 {
        padding-bottom: 2em; }

    &_pb3 {
        padding-bottom: 3em; }

    &_pb4 {
        padding-bottom: 4em; }

    &_pb5 {
        padding-bottom: 5em; } }
